<template>
    <v-container style="width: 100%; " align="center" fluid>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                dense
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-center pa-0">
                    <v-toolbar-title>{{eventsText}}</v-toolbar-title>
                </v-col>
            </v-row>
            <template class="pa-0 app-bar-gradient-back" v-slot:extension style="width: 100%!important">
                <v-tabs v-model="eventsTabs" background-color="transparent">
                    <v-tab key="list" style="width: 50%!important; min-width: 50%!important;">
                        <v-icon>mdi-card-text</v-icon>
                    </v-tab>
                    <v-tab key="map" style="width: 50%!important; min-width: 50%!important;">
                        <v-icon>mdi-map</v-icon>
                    </v-tab>
                </v-tabs>
            </template>
        </v-app-bar>

        <v-tabs-items v-model="eventsTabs" style="background: transparent">
            <v-tab-item key="list" style="margin-top: 94px; background: transparent;">
                <v-row class="pa-0" dense style="padding-left: 12px; padding-right: 12px">
                    <v-select
                            v-model="languageInvolvedFilter"
                            style="margin-top: 15px; padding-bottom: 0px!important;"
                            :items="languages"
                            item-value="id"
                            item-text="option"
                            solo
                            dense
                            return-object
                    >

                        <template v-slot:item="{item}">
                            <v-avatar>
                                <img :src="createPathToFlagImage(item.id)"
                                     style="height: 60%; width: auto"/>
                            </v-avatar>
                            {{ item.text }}
                        </template>
                        <template v-slot:selection="{item, select, selected }">
                            <v-avatar>
                                <img :src="createPathToFlagImage(item.id)"
                                     style="height: 60%; width: auto"/>
                            </v-avatar>
                            {{ item.text }}
                        </template>
                    </v-select>
                </v-row>
                <v-row v-if="events.length > 0" dense
                       class="text-center justify-center"
                       style="padding-bottom: 80px">
                    <div
                            style="margin-bottom: 10px; max-width: 100%"
                            v-for="event in events"
                            :key="event.id"
                            class="pa-2"
                    >
                        <v-card
                                max-width="400"
                                width="100%"
                                min-width="280"
                                @click="openEventDescription(event.id)"
                                style="display: inline-block;"
                        >
                            <v-img
                                    class="white--text align-end"
                                    height="150px"
                                    :src="event.headerImg"
                                    style="position: relative"
                            >

                                <v-chip
                                        v-if="userLocation != null && event.distance != null"
                                        style="display:inline; float:right; margin-right: 5px; bottom: 5px"
                                        color="primary"
                                        small
                                >
                                    <p class="text-with-icon ma-1">
                                        <v-icon style="display: inline; vertical-align: top; font-size:18px;">
                                            mdi-map-marker-outline
                                        </v-icon>
                                        {{event.distance.toFixed(2)}} km
                                    </p>
                                </v-chip>
                            </v-img>

                            <v-card-text class="text--primary" style="padding-bottom: 2px!important;">

                                <div style="display: flex; position: relative;">
                                    <div style="margin-top: 10.5px" class="secondary--text">
                                        <strong style="font-size: 46px;">{{getDay(event.date)}}</strong>
                                    </div>
                                    <div style="margin-left: 5px" class="secondary--text">
                                        <div>
                                            <strong>{{getMonth(event.date)}}</strong>
                                        </div>
                                        <div>
                                            <strong>{{getYear(event.date)}}</strong>
                                        </div>
                                    </div>
                                    <div style="margin-left: 25px" class="text-left">
                                        <div class="secondary--text">
                                            <strong style="font-size: 18px; margin-bottom: 0px">"{{event.name}}"</strong>
                                        </div>
                                        <div>
                                            <p class="text-with-icon">
                                                <v-icon style="display: inline;  vertical-align: top; font-size:18px;">
                                                    mdi-store
                                                </v-icon>
                                                {{event.location.place}}<br>
                                                ({{event.location.city}})
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <v-spacer></v-spacer>
                            </v-card-text>
                        </v-card>
                    </div>
                    <div
                            v-if="userRole == 'admin'"
                            v-scroll="onScroll"
                            style="height: 80px; width: 100%">
                    </div>
                    <v-btn
                            v-if="userRole != 'admin'"
                            v-scroll="onScroll"
                            style="z-index: 3; bottom: 80px"
                            v-show="fabScroll && events.length > 0"
                            fab
                            dark
                            fixed
                            right
                            color="primary"
                            @click="toTop"
                    >
                        <v-icon>mdi-arrow-up</v-icon>
                    </v-btn>
                    <v-btn
                            v-if="userRole == 'admin'"
                            v-scroll="onScroll"
                            style="z-index: 3; bottom: 150px"
                            v-show="fabScroll && events.length > 0"
                            fab
                            dark
                            fixed
                            right
                            color="primary"
                            @click="toTop"
                    >
                        <v-icon>mdi-arrow-up</v-icon>
                    </v-btn>
                </v-row>

                <v-row v-if="pageLoading" class="text-center justify-center">
                    <v-progress-circular
                            indeterminate
                            color="primary"
                            style="margin-bottom: 50px"
                    ></v-progress-circular>
                </v-row>

                <v-card v-if="events.length == 0 && eventsListed" class="pa-2 ma-1"
                        style="height: calc(100vh - 276px); margin-top:-16px!important;">
                    <v-row class="text-center justify-center">
                        <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noEventsText}}</p>
                    </v-row>
                    <v-row class="text-center justify-center">
                        <img :src="sadFaceImg"
                             style="opacity: 0.2; width: 12vh; height: 12vh; max-width: 150px; min-width: 65px; max-height: 150px; min-height: 65px">
                    </v-row>
                </v-card>
            </v-tab-item>
            <v-tab-item key="map" style="background: transparent">
                <v-card v-if="userLocation != null" style="top: 110px">
                    <v-card-actions class="pa-0 justify-center">
                        <GmapMap
                                class="ma-2"
                                :center="userLocation"
                                :zoom="14"
                                style="width:98%;  height: calc(100vh - 228px); min-height: 240px"
                        >
                            <GmapMarker
                                    :position="userLocation"
                                    :icon="userPinIcon"/>
                            <GmapMarker
                                    v-for="event in mapEvents"
                                    :key="event.id"
                                    :position="event.location.coordinates"
                                    :clickable="true"
                                    @click="openEventDescription(event.id)"
                                    :icon="eventPinIcon"
                            />
                        </GmapMap>
                    </v-card-actions>
                </v-card>

                <v-card v-if="userLocation == null" class="pa-2 ma-1"
                        style="height: calc(100vh - 228px); top: 110px">
                    <v-row class="text-center justify-center">
                        <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noLocationPermText}}</p>
                    </v-row>
                    <v-row class="text-center justify-center">
                        <img :src="sadFaceImg"
                             style="opacity: 0.2; width: 12vh; height: 12vh; max-width: 150px; min-width: 65px; max-height: 150px; min-height: 65px">
                    </v-row>
                </v-card>
            </v-tab-item>
        </v-tabs-items>

        <v-btn
                v-if="userRole == 'admin'"
                style="z-index: 3; bottom: 80px"
                fab
                dark
                fixed
                right
                color="primary"
                @click="createEvent"
        >
            <v-icon>mdi-plus</v-icon>
        </v-btn>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions, mapMutations, mapGetters} from 'vuex';

    import Constants from "../../store/Constants";
    import {gmapApi} from 'vue2-google-maps'

    // Página de visualización de eventos de intercambio de idiomas en formato de lista y de mapa
    // @group Eventos
    export default {
        name: "EventsHome",
        data() {
            return {
                buttonIsDisabled: true,
                center: {lat: 45.508, lng: -73.587},
                markers: [],
                places: [],
                currentPlace: null,
                eventsText: null,
                noEventsText: null,
                sadFaceImg: null,
                eventsListed: false,
                languageInvolvedFilter: null,
                languages: null,
                eventLanguageText: null,
                youText: null,
                eventsTabs: null,
                mapEventsListed: false,
                noLocationPermText: null,
                pageLoading: false,
                fabScroll: false,
                snackbarText: null,
                snackbar: false,
                closeText: null
            };
        },

        methods: {
            ...mapActions('events', ['getEvents', 'resetState', 'resetEventsToShowIDs']),
            ...mapMutations('events', ['setLanguageInvolvedFilter']),

            // @vuese
            // Redirige a la página de creación de un evento
            createEvent: function () {
                this.$router.push({name: 'eventCreate'});
            },

            // @vuese
            // Detecta cuando se ha realizado scroll en la página
            onScroll(e) {
                if (typeof window === 'undefined') return
                const top = window.pageYOffset || e.target.scrollTop || 0
                this.fabScroll = top > 20
            },

            // @vuese
            // Realiza un scroll automático al inicio de la página
            toTop() {
                this.$vuetify.goTo(0)
            },

            // @vuese
            // Devuelve el mes en formato i18n de texto abreviado de la fecha pasada como parámetro
            // @arg El parámetro es un Date que representa la fecha del evento
            getMonth(date) {
                return this.$i18n.d(date.toDate(), 'abbrMonth').toUpperCase();
            },

            // @vuese
            // Devuelve el día del mes de la fecha pasada como parámetro
            // @arg El parámetro es un Date que representa la fecha del evento
            getDay(date) {
                return date.toDate().getDate();
            },

            // @vuese
            // Devuelve el año de la fecha pasada como parámetro
            // @arg El parámetro es un Date que representa la fecha del evento
            getYear(date) {
                return date.toDate().getFullYear();
            },

            // @vuese
            // Obtiene los eventos a presentar en la vista de lista de eventos que cumplen con lo requerido por el usuario autenticado de forma paginada
            listEvents: function () {
                let self = this;
                self.buttonIsDisabled = true;
                let resultGetEvents = self.getEvents();

                resultGetEvents.then((moreEventsToShow) => {
                    self.buttonIsDisabled = !moreEventsToShow;
                    self.pageLoading = false;
                })
                    .catch((error) => {
                        self.pageLoading = false;
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },

            createPathToFlagImage: function (lang) {
                return "../img/flags/" + lang + ".svg";
            },

            // @vuese
            // Redirige a la página de visualización del evento con el ID pasado como parámetro
            // @arg El parámetro es un String que representa el ID del evento seleccionado por el usuario
            openEventDescription(eventID) {
                this.$router.push('event/' + eventID);
            }
        },
        computed: {
            ...mapGetters('events', {events: 'getEventsToShow'}),
            ...mapGetters('events', {mapEvents: 'getMapEventsToShow'}),
            google: gmapApi,

            // @vuese
            // Devuelve el rol del usuario (admin o user)
            userRole: function () {
                return this.$store.getters['user/getRole'];
            },

            // @vuese
            // Devuelve el pin a situar en el mapa del usuario autenticado
            userPinIcon: function () {
                return {
                    url: "https://imagehost.com.au//AdWES", // url
                    scaledSize: this.google && new this.google.maps.Size(39, 41)
                };
            },

            // @vuese
            // Devuelve el pin del evento a situar en el mapa
            eventPinIcon: function () {
                return {
                    url: "https://imagehost.com.au//8bS3m", // url
                    scaledSize: this.google && new this.google.maps.Size(39, 41)
                };
            },

            // @vuese
            // Devuelve la geolocalización del usuario autenticado
            userLocation: function () {
                return this.$store.getters['user/getLocation'];
            }
        },
        beforeMount() {
            let self = this;
            self.languages = self.$store.getters['user/getLanguageOptionsAsArray'];
        },

        created: async function () {
            let self = this;

            this.eventsText = this.$i18n.t('messages.TEXT_EVENTS');
            this.noEventsText = this.$i18n.t('messages.TEXT_NO_EVENTS_AVAILABLE');
            this.eventLanguageText = this.$i18n.t('messages.TEXT_LANGUAGE_INVOLVED_IN_EVENTS');
            this.youText = this.$i18n.t('messages.TEXT_YOU');
            this.noLocationPermText = this.$i18n.t('messages.TEXT_LOCATION_PERMISSION_NOT_GRANTED');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.sadFaceImg = Constants.IMG_EMOJI_SAD_FACE;

            let $ = require('jquery');
            await self.resetEventsToShowIDs();
            self.eventsListed = false;

            if (self.events.length == 0) {
                let available = true;
                self.buttonIsDisabled = true;

                let first = true;

                while (first || (available && $(document).height() <= window.screen.height)) {
                    first = false;
                    await self.getEvents().then((moreEventsToShow) => {
                        available = moreEventsToShow;
                        self.languageInvolvedFilter = self.$store.getters['events/getLanguageInvolvedFilter'];
                        self.pageLoading = false;
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                            self.pageLoading = false;
                        });
                }

                self.buttonIsDisabled = !available;
            }
            self.eventsListed = true;
        },

        mounted() {
            let self = this;
            let $ = require('jquery');

            $(window).scroll(function () {
                let bottom_position = $(document).height() - ($(window).scrollTop() + window.screen.height);

                if (!self.buttonIsDisabled && bottom_position < 1) {
                    self.pageLoading = true;
                    self.listEvents();
                }
            });

            self.eventsTabs = self.$store.getters['events/getEventsTab'];
        },
        watch: {
            // @vuese
            // Observa los eventos a presentar en el mapa para controlar si ya se han cargado
            mapEvents: function () {
                if (this.mapEvents.length > 0) {
                    self.mapEventsListed = true;
                }
            },

            // @vuese
            // Devuelve la tab seleccionada de entre las dos disponibles y que se corresponden con la vista de mapa y la vista de lista de los eventos
            eventsTabs: function () {
                this.$store.commit('events/setEventsTab', this.eventsTabs);
            },

            // @vuese
            // Controla la coherencia en el filtrado de los idiomas del evento
            languageInvolvedFilter: async function (newLanguage, oldLanguage) {
                let selectedInvolvedLanguage;
                let oldSelectedInvolvedLanguage;

                if (oldLanguage != null && oldLanguage.hasOwnProperty("id")) {
                    oldSelectedInvolvedLanguage = oldLanguage.id;
                } else {
                    oldSelectedInvolvedLanguage = oldLanguage;
                }

                if (newLanguage.hasOwnProperty("id")) {
                    selectedInvolvedLanguage = newLanguage.id;
                } else {
                    selectedInvolvedLanguage = newLanguage;
                }

                let self = this;

                if (oldSelectedInvolvedLanguage != null && oldSelectedInvolvedLanguage != selectedInvolvedLanguage) {
                    let $ = require('jquery');
                    self.eventsListed = false;
                    self.resetEventsToShowIDs();
                    self.setLanguageInvolvedFilter(selectedInvolvedLanguage);

                    let available = true;
                    let first = true;
                    self.buttonIsDisabled = true;

                    while (first || (available && $(document).height() <= $(window).height())) {
                        first = false;
                        await self.getEvents().then((moreEventsToShow) => {
                            available = moreEventsToShow;
                            self.pageLoading = false;
                        })
                            .catch((error) => {
                                self.snackbarText = error;
                                self.snackbar = true;
                                self.pageLoading = false;
                            });
                    }

                    self.buttonIsDisabled = !available;
                    self.eventsListed = true;
                }
            }
        }
    }
</script>

<style scoped>
</style>